:root {
  --footer-size: 2rem;
}

.left {
  text-align: left;
}

pre > code.pre-code-l {
  max-height: 500px;
}

.reveal .green {
  color: #17ff2e;
}

.reveal .red {
  color: red;
}

.scrollable-slide {
  height: 100%;
  overflow-y: auto !important;
}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}
::-webkit-scrollbar-thumb {
  background-color: #333;
}
::-webkit-scrollbar-corner {
  background-color: #333;
}

.footer {
  position: absolute;
  display: flex;
  bottom: 0;
  max-height: var(--footer-size, 32px);
  width: 100%;
  flex-direction: row-reverse;
}

.footer img {
  max-height: var(--footer-size, 32px);
}

.ukraine {
  color: #fff;
  line-height: 1em;
  overflow-wrap: anywhere;
  background: linear-gradient(to bottom, #015bbb 50%, #fed500 50%);
  padding: 1.5rem 0.5rem;
}

.two-column {
  display: flex;
  text-align: left;
  flex-direction: row;
}
